import React, { memo } from 'react';
import PropTypes from 'prop-types';
import { Helmet } from 'react-helmet-async';
import Navbar from '../../shared/ui/NavBar';
import Footer from '../../shared/ui/Footer';
import { MysticBackground } from '../components/common';

/**
 * Layout chung cho ton b trang
 * @param {Object} props - Props ca component
 * @param {React.ReactNode} props.children - Ni dung ca trang
 * @param {string} props.title - Tiu  trang
 * @param {string} props.description - M t trang cho SEO
 * @param {Object} props.meta - Cc th meta b sung
 * @param {string} props.className - Classes b sung cho container
 */
const PageLayout = memo(({ 
  children, 
  title = 'Bi Tarot Online',
  description = 'Khm ph tng lai vi Bi Tarot Online - t tnh yu, s nghip n sc khe v pht trin bn thn.',
  meta = {},
  className = ''
}) => {
  return (
    <div className="min-h-screen bg-gradient-to-b from-[#1a0933] to-[#0f051d] text-white relative overflow-hidden">
      <Helmet>
        <title>{title} | Bi Tarot</title>
        <meta name="description" content={description} />
        {Object.entries(meta).map(([name, content]) => (
          <meta key={name} name={name} content={content} />
        ))}
      </Helmet>
      
      <MysticBackground />
      <Navbar />
      
      <main className={`relative pt-32 pb-20 px-4 md:px-8 ${className}`}>
        <div className="container mx-auto max-w-6xl relative z-10">
          {children}
        </div>
      </main>
      
      <Footer />
    </div>
  );
});

PageLayout.propTypes = {
  children: PropTypes.node.isRequired,
  title: PropTypes.string,
  description: PropTypes.string,
  meta: PropTypes.object,
  className: PropTypes.string
};

export default PageLayout; 