import React, { useState } from 'react';
import TarotReading from '../../features/tarot/components/TarotReading';
import PageLayout from '../../shared/layouts/PageLayout';
import { 
  SectionTitle, 
  FilterButton, 
  SearchBar, 
  ReadingTypeCard, 
  TestimonialCard, 
  HowItWorksStep,
  Button,
  Card
} from '../../shared/components/common';

const TarotReadingsPage = () => {
  const [searchTerm, setSearchTerm] = useState('');
  const [activeFilter, setActiveFilter] = useState('all');
  const [showFullTarotReading, setShowFullTarotReading] = useState(false);
  
  // Sample data - would normally come from API/database
  const readingTypes = [
    {
      id: 1,
      title: "Bi tnh yu",
      description: "Khm ph ng tnh duyn, tng lai ca mi quan h hin ti hoc ngi y ngh g v bn.",
      iconSrc: "https://api-prod-minimal-v510.vercel.app/assets/images/avatar/avatar_8.jpg",
      to: "/love",
      featured: true,
      categories: ["tnh yu", "ph bin"]
    },
    {
      id: 2,
      title: "Bi s nghip",
      description: "Tm hiu v con ng s nghip, c hi mi v hng i ng n cho cng vic ca bn.",
      iconSrc: "https://api-prod-minimal-v510.vercel.app/assets/images/avatar/avatar_9.jpg",
      to: "/career",
      featured: false,
      categories: ["s nghip", "cng vic"]
    },
    {
      id: 3,
      title: "Bi sc khe",
      description: "Nhn li khuyn v sc khe th cht v tinh thn, cnh bo v cch ci thin sc khe.",
      iconSrc: "https://api-prod-minimal-v510.vercel.app/assets/images/avatar/avatar_10.jpg",
      to: "/health",
      featured: false,
      categories: ["sc khe"]
    },
    {
      id: 4,
      title: "Bi hng ngy",
      description: "L bi Tarot hng ngy gip bn c ci nhn tng quan v li khuyn cho mt ngy trn vn.",
      iconSrc: "https://api-prod-minimal-v510.vercel.app/assets/images/avatar/avatar_11.jpg",
      to: "/daily",
      featured: true,
      categories: ["hng ngy", "ph bin"]
    },
    {
      id: 5,
      title: "Bi nm mi",
      description: "D on v ln k hoch cho nm mi ca bn vi nhng li khuyn t bi Tarot.",
      iconSrc: "https://api-prod-minimal-v510.vercel.app/assets/images/avatar/avatar_12.jpg", 
      to: "/yearly",
      featured: false,
      categories: ["nm mi", "tng lai"]
    },
    {
      id: 6,
      title: "Phn tch tnh cch",
      description: "Khm ph im mnh, im yu v tim nng n su trong tnh cch ca bn.",
      iconSrc: "https://api-prod-minimal-v510.vercel.app/assets/images/avatar/avatar_13.jpg",
      to: "/personality",
      featured: false,
      categories: ["tnh cch", "pht trin bn thn"]
    }
  ];

  const filters = [
    { id: 'all', label: 'Tt c' },
    { id: 'ph bin', label: 'Ph bin' },
    { id: 'tnh yu', label: 'Tnh yu' },
    { id: 's nghip', label: 'S nghip' },
    { id: 'sc khe', label: 'Sc khe' },
    { id: 'tng lai', label: 'Tng lai' }
  ];

  const testimonials = [
    {
      quote: "Bi Tarot  gip ti tm ra hng i mi trong s nghip, tht khng ng li chnh xc n vy.",
      author: "Minh Anh, 28 tui"
    },
    {
      quote: "Ti  hiu r hn v mi quan h ca mnh nh vo phn bi tnh yu. Cm n Bi Tarot!",
      author: "Thu H, 32 tui"
    }
  ];

  const filteredReadingTypes = readingTypes.filter(reading => {
    const matchesSearch = reading.title.toLowerCase().includes(searchTerm.toLowerCase()) || 
                        reading.description.toLowerCase().includes(searchTerm.toLowerCase());
    
    const matchesFilter = activeFilter === 'all' || reading.categories.includes(activeFilter);
    
    return matchesSearch && matchesFilter;
  });

  return (
    <PageLayout
      title="Bi Tarot Online"
      description="Khm ph cc loi hnh bi Tarot online chnh xc - t tnh yu, s nghip n sc khe v pht trin bn thn."
    >
      {/* Hero Section */}
      <div className="text-center mb-16">
        <h1 className="text-4xl md:text-5xl lg:text-6xl font-bold mb-6 tracking-vn-tight">
          Khm Ph <span className="text-[#9370db]">Bi Tarot</span> Online
        </h1>
        <p className="text-lg md:text-xl text-gray-300 max-w-3xl mx-auto tracking-vn-tight leading-vn">
          Nhng l bi Tarot s mang n cho bn ci nhn su sc v cuc sng, t tnh yu, s nghip 
          n sc khe, tng lai v pht trin bn thn.
        </p>
        
        <div className="mt-10">
          {!showFullTarotReading ? (
            <Button 
              onClick={() => setShowFullTarotReading(true)} 
              variant="primary" 
              size="lg"
            >
              Bt u xem bi ngay
            </Button>
          ) : (
            <div className="max-w-3xl mx-auto">
              <TarotReading />
            </div>
          )}
        </div>
      </div>
      
      {!showFullTarotReading && (
        <>
          {/* Search and Filter */}
          <div className="mb-12">
            <SearchBar 
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              placeholder="Tm kim loi hnh bi Tarot..."
            />
            
            <div className="flex flex-wrap mb-8">
              {filters.map(filter => (
                <FilterButton
                  key={filter.id}
                  label={filter.label}
                  active={activeFilter === filter.id}
                  onClick={() => setActiveFilter(filter.id)}
                />
              ))}
            </div>
          </div>
          
          {/* Reading Types Grid */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {filteredReadingTypes.length > 0 ? (
              filteredReadingTypes.map(reading => (
                <ReadingTypeCard
                  key={reading.id}
                  title={reading.title}
                  description={reading.description}
                  iconSrc={reading.iconSrc}
                  to={reading.to}
                  featured={reading.featured}
                />
              ))
            ) : (
              <div className="col-span-3 py-8 text-center">
                <p className="text-gray-300 tracking-vn-tight mb-4">Khng tm thy loi bi ph hp vi tm kim ca bn.</p>
                <Button
                  onClick={() => {setSearchTerm(''); setActiveFilter('all');}}
                  variant="secondary"
                >
                  Xem tt c cc loi bi
                </Button>
              </div>
            )}
          </div>
          
          {/* How It Works Section */}
          <div className="mt-32">
            <SectionTitle 
              title="Cch Bi Tarot Hot ng" 
              subtitle="Quy trnh n gin  khm ph nhng thng ip t bi Tarot"
              light={false}
              centered
            />
            
            <div className="grid grid-cols-1 md:grid-cols-2 gap-10 mt-12">
              <div className="space-y-10">
                <HowItWorksStep 
                  number="1"
                  title="Chn loi hnh bi"
                  description="La chn hnh thc ph hp vi cu hi hoc mi quan tm ca bn, t tnh yu n s nghip."
                />
                
                <HowItWorksStep 
                  number="2"
                  title="t cu hi"
                  description="t cu hi c th hoc xc nh vn  bn ang tm kim cu tr li."
                />
                
                <HowItWorksStep 
                  number="3"
                  title="Lt bi v c kt qu"
                  description="Cc l bi s c lt ln v gii thch  ngha, mang n cho bn ci nhn su sc."
                />
              </div>
              
              <Card title="Tri nghim t khch hng">
                <div className="space-y-4">
                  {testimonials.map((testimonial, index) => (
                    <TestimonialCard
                      key={index}
                      quote={testimonial.quote}
                      author={testimonial.author}
                    />
                  ))}
                </div>
              </Card>
            </div>
          </div>
        </>
      )}
      
      {/* FAQ Section */}
      {!showFullTarotReading && (
        <section className="py-20 bg-gradient-to-b from-[#0f051d] to-[#1a0933] mt-20 -mx-8 px-8">
          <div className="container mx-auto max-w-5xl">
            <SectionTitle 
              title="Cu Hi Thng Gp" 
              subtitle="Nhng iu bn mun bit v Bi Tarot Online"
              light={false}
              centered
            />
            
            <div className="mt-12 grid grid-cols-1 md:grid-cols-2 gap-8">
              <Card>
                <h3 className="text-lg font-bold mb-3 tracking-vn-tight">Bi Tarot online c chnh xc khng?</h3>
                <p className="text-gray-300 tracking-vn-tight leading-vn">
                  Bi Tarot online s dng cc thut ton  m phng cch tri bi truyn thng v cung cp li gii thch da trn  ngha 
                  ca l bi.  chnh xc ph thuc vo nhiu yu t, bao gm c cch bn hiu v p dng li khuyn.
                </p>
              </Card>
              
              <Card>
                <h3 className="text-lg font-bold mb-3 tracking-vn-tight">Bao lu ti nn bi Tarot mt ln?</h3>
                <p className="text-gray-300 tracking-vn-tight leading-vn">
                  Khng c quy tc c nh, nhng nhiu ngi chn bi hng ngy  c hng dn, 
                  hoc nh k hng tun/thng cho cc vn  ln hn. Nn trnh bi qu nhiu v cng mt vn .
                </p>
              </Card>
              
              <Card>
                <h3 className="text-lg font-bold mb-3 tracking-vn-tight">C cn kin thc v Tarot khng?</h3>
                <p className="text-gray-300 tracking-vn-tight leading-vn">
                  Khng, bn khng cn kin thc trc v Tarot. H thng ca chng ti s cung cp y  thng tin gii thch 
                  v  ngha ca cc l bi v cch hiu kt qu tri bi.
                </p>
              </Card>
              
              <Card>
                <h3 className="text-lg font-bold mb-3 tracking-vn-tight">Ti c th hi bt c iu g khng?</h3>
                <p className="text-gray-300 tracking-vn-tight leading-vn">
                  Bn c th t cu hi v hu ht cc kha cnh ca cuc sng, t tnh yu, gia nh, 
                  s nghip n pht trin c nhn. Tuy nhin, nn trnh nhng cu hi v vn  sc khe nghim trng hoc php l.
                </p>
              </Card>
            </div>
            
            <div className="text-center mt-16">
              <Button
                onClick={() => setShowFullTarotReading(true)}
                variant="primary"
                size="lg"
              >
                Bt u tri bi ngay
              </Button>
            </div>
          </div>
        </section>
      )}
    </PageLayout>
  );
};

export default TarotReadingsPage;